import React, { useState } from "react";
import Calculator from "./Calculator";
import History from "./History";

import "./Calculator.css";

const Main = () => {
  const [inHome, setInHome] = useState(true);

  const passPage = () => {
    setInHome(false);
  };

  const backPage = () => {
    setInHome(true);
  };

  return (
    <section id="id-body">
      {inHome ? <Calculator confirmAction={() => passPage()} /> : null}
      {!inHome ? <History backAction={() => backPage()} /> : null}
    </section>
  );
};

export default Main;
